.specialties {
  &__slider {
    width: 300%;
    height: 556px;
    display: flex;
    transform: translate(0vw);
    transition-duration: 2s;
    transition-timing-function: ease;

    &__info {
      width: 100vw;
      height: 556px;
      display: flex;
      justify-content: space-around;
      align-items: center;
      z-index: 1;

      & img {
        filter: drop-shadow(0px 0px 50px #000000);
      }
    }

    &__article {
      max-width: 40%;
      color: #ffffff;
      z-index: 1;

      & > h1 {
        color: #ffffff;
      }
    }
  }

  display: block;
  width: 100%;
  height: 676px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("./../../img/specialties-bg.jpg") no-repeat;
  background-position: center;
  background-size: cover;

  &__decoration {
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 676px;
    z-index: 0;
    position: absolute;
  }

  &__label {
    height: 60px;
    font-family: OpenSans;
    font-weight: 700;
    font-size: 12px;
    line-height: 20px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    align-items: center;
    text-align: center;
    letter-spacing: 0.5em;
    text-transform: uppercase;
    color: #ffffff;
    z-index: 1;
  }

  &__control {
    display: flex;
    position: absolute;
    height: 60px;
    width: 100%;
    z-index: 1;

    &__block {
      display: flex;
      margin: auto;
      margin-bottom: 50px;
      justify-content: space-between;
      width: 70px;
      height: 10px;

      &__button {
        display: none;
        &:checked + &__label::before {
          background-color: #ffffff;
        }

        &__label {
          &::before {
            content: "";
            display: inline-block;
            width: 10px;
            height: 10px;
            background: rgba(255, 255, 255, 0.5);
            border-radius: 50%;
            cursor: pointer;
          }

          &:hover::before {
            background-color: rgba(255, 255, 255, 0.75);
          }
        }
      }
    }
  }
}
